<template>
  <div class="chapters" :style="{backgroundColor:attributeData.bgc,color:attributeData.fontc}">
    <van-nav-bar
      :style="{backgroundColor:attributeData.toolc,color:attributeData.fontc}"
      v-show="isShow"
      left-arrow
      :title="bookDetail.title"
      fixed
      @click-left="back"
    />
    <div class="tool-box" :style="{backgroundColor:attributeData.toolc}" v-show="isShow">
      <div class="changeOrder">
        <div class="prev" @click="goPrevChapters">上一章</div>
        <div class="next" @click="goNextChapters">下一章</div>
      </div>
      <div class="btns">
        <div class="btn directort" @click="showPopup">
          <div class="icon">
            <i class="fa fa-list" aria-hidden="true"></i>
          </div>目录
        </div>
        <div class="btn sunOrmoon" @click="changeSun">
          <div class="icon">
            <i :class="isSun?'fa fa-moon-o':'fa fa-sun-o'" aria-hidden="true"></i>
          </div>
          {{isSun?'夜间':'白天'}}
        </div>
        <div class="btn readSet" @click="readSet">
          <div class="icon">
            <i class="fa fa-cog" aria-hidden="true"></i>
          </div>阅读设置
        </div>
      </div>
    </div>

    <div class="readSet-box" :style="{backgroundColor:attributeData.toolc}" v-show="isReadSet">
      <div class="font-size row">
        <span>字号</span>
        <div class="row-btns">
          <div class="btn-fontchange" @click="fontSizeDown">A-</div>
          <span>{{attributeData.fontSize}}</span>
          <div class="btn-fontchange" @click="fontSizeUp">A+</div>
        </div>
      </div>
      <div class="background-color row">
        <span>背景</span>
        <div class="row-btns">
          <div
            class="btn-color"
            :class="{'active':item.active}"
            :style="{backgroundColor:item.bgc}"
            v-for="(item,index) in attributeData.bgColor"
            :key="index"
            @click="changeBgc(index)"
          >
            <van-icon name="success" v-show="item.active" />
          </div>
        </div>
      </div>
      <div class="line-height row">
        <span>间距</span>
        <div class="row-btns">
          <div class="btn-line" @click="lineHeightDown">
            <i class="fa fa-bars" aria-hidden="true"></i>
            <i class="fa fa-long-arrow-down" aria-hidden="true"></i>
          </div>
          <div class="btn-line" @click="lineHeightUp">
            <i class="fa fa-align-justify" aria-hidden="true"></i>
            <i class="fa fa-long-arrow-up" aria-hidden="true"></i>
          </div>
        </div>
      </div>
      <div class="change-way row">
        <span>翻页</span>
        <div class="row-btns">
          <div
            class="btn-way"
            :class="{'active':item.active}"
            v-for="(item,index) in attributeData.changeWay"
            :key="index"
            @click="changeWay(index)"
          >{{item.text}}</div>
        </div>
      </div>
      <div class="cancel" @click="showTool">取消</div>
    </div>

    <div class="showTool" v-show="isSideMole">
      <div class="showTool-top" @click="prevPage"></div>
      <div class="showTool-bottom" @click="nextPage"></div>
      <div class="showTool-center" @click="showTool"></div>
    </div>
    <div class="chapters-title">
      <p>{{chapterText.title}}</p>
    </div>
    <div class="view-box" @click="showTool" ref="viewBox">
      <div class="chapters-text">
        <div class="chapters-item" v-for="(item,index) in chapterTextList" :key="index">
          <h2>{{item.title}}</h2>
          <div
            class="book-chapTexts"
            :style="{fontSize:attributeData.fontSize+'px',lineHeight:attributeData.lineHeight}"
            v-for="(v,i) in item.cpContent"
            :key="i"
          >{{v}}</div>
        </div>
      </div>
    </div>
    <div class="chapters-footer">
      <div class="time">{{nowTime | formatDate('hh:mm:ss')}}</div>
      <div class="range">{{bookState.bookOrder+1}} / {{bookDetail.chaptersCount}}</div>
    </div>
    <van-popup v-model="popShow" position="left" :style="{ height: '100%', width:'80%'}">
      <div class="popTop">
        <h2>{{bookDetail.title}}</h2>
        <div class="directory-sort">
          <span>{{bookDetail.allowMonthly?"已完结 共":"连载中 更新至"}}{{bookDetail.chaptersCount}}章</span>
          <span @click="changeSort" v-if="isSort">
            <van-icon name="ascending" size="16px" color="#f99222" />升序
          </span>
          <span @click="changeSort" v-else>
            <van-icon name="descending" size="16px" color="#f99222" />降序
          </span>
        </div>
      </div>
      <div class="popList">
        <div class="poplist" ref="poplist">
          <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
            <van-cell
              v-for="(item,index) in list"
              :key="index"
              :title="item.title"
              :class="index==bookState.bookOrder?'active':''"
              @click="goChapters(item)"
            >
              <template #right-icon>
                <span v-if="item.isVip">
                  <van-icon name="lock" />
                </span>
                <span class="vip-text" v-else>免费</span>
              </template>
            </van-cell>
          </van-list>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import "../assets/less/chapters.less";

export default {
  data() {
    return {
      chapterTextList: [],
      popShow: false,
      isShow: false,
      isReadSet: false,
      isSun: true,
      scrollTop: "",
      nowTime: new Date(),
      attributeData: {
        fontSize: 16,
        bgc: "#f6f6f6",
        fontc: "#000000",
        toolc: "#ffffff",
        bgColor: [
          {
            bgc: "#f6f6f6",
            fontc: "#000000",
            toolc: "#ffffff",
            active: true,
          },
          {
            bgc: "#dbe6c4",
            fontc: "#000000",
            toolc: "#ddfddf",
            active: false,
          },
          {
            bgc: "#f6d898",
            fontc: "#000000",
            toolc: "#feefee",
            active: false,
          },
          {
            bgc: "#e7e2c5",
            fontc: "#000000",
            toolc: "#eeeeee",
            active: false,
          },
          {
            bgc: "#463e3c",
            fontc: "#f6f6f6",
            toolc: "#666666",
            active: false,
          },
        ],
        lineHeight: 2,
        changeWay: [
          {
            text: "滑动",
            active: true,
            way:false,
          },
          {
            text: "点击",
            active: false,
            way:true
          },
        ],
      },
      isSort: true,
      dirList: [],
      list: [],
      loading: false,
      finished: false,

      isSideMole: false,
    };
  },
  created() {
    this.getNowTime();
    this.chapterTextList.push(this.chapterText);
    this.directoryList.map((v) => {
      this.dirList.push(v);
    });
    // console.log("chapterText==>", this.chapterText);
    // console.log("chaptersList==>", this.chaptersList);
    // console.log("chapterHistory==>", this.chapterHistory);
    // console.log("bookState==>", this.bookState);
  },
  computed: {
    chapterText() {
      return this.$store.state.chaptersStore.chapterText;
    },
    chaptersList() {
      return this.$store.state.chaptersStore.chaptersList;
    },
    chapterHistory() {
      return this.$store.state.chaptersStore.chapterHistory;
    },
    // 书籍详情
    bookDetail() {
      return this.$store.state.detailStore.bookDetail;
    },
    // 书籍状态
    bookState() {
      return this.$store.state.detailStore.bookDetail.bookState;
    },
    // 目录
    directoryList() {
      return this.$store.state.detailStore.directoryList;
    },
  },
  mounted() {
    this.$refs.viewBox.addEventListener(
      "scroll",
      () => {
        let box = this.$refs["viewBox"];
        if (box.scrollHeight == box.scrollTop + box.offsetHeight) {
          // console.log("到底了");
          this.getNextChapters();
        }
        this.scrollTop = box.scrollHeight;
        this.isShow = false;
        this.isReadSet = false;
      },
      true
    );
  },
  methods: {
    // 返回
    back() {
      this.$router.go(-1);
    },

    // 点击屏幕显示控制区域
    showTool() {
      if (this.isReadSet) {
        this.isShow = false;
        this.isReadSet = false;
        return;
      }
      this.isShow = !this.isShow;
      this.isReadSet = false;
    },

    // 上一页
    prevPage() {
      let box = this.$refs["viewBox"];
      box.scrollTop -= box.offsetHeight * 0.9;
      if (box.scrollTop <= 0) box.scrollTop = 0;
    },

    // 下一页
    nextPage() {
      let box = this.$refs["viewBox"];
      box.scrollTop += box.offsetHeight * 0.9;
    },

    // 打开阅读设置界面
    readSet() {
      this.isReadSet = true;
      this.isShow = false;
    },

    // 渲染当前时间
    getNowTime() {
      setInterval(() => {
        this.nowTime = new Date();
      }, 1000);
    },

    // 滚动获取下一章
    getNextChapters() {
      // console.log(this.$store.state.directoryList,this.chapterHistory,this.chapterText)
      if (
        this.bookState.bookOrder + 1 >=
        this.$store.state.detailStore.directoryList.length
      ) {
        this.$toast("没有下一章了");
        return;
      }
      let link = this.$store.state.detailStore.directoryList[
        this.bookState.bookOrder + 1
      ].link;

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/chapters/" + encodeURIComponent(link),
      }).then((result) => {
        this.$toast.clear();
        // console.log(result);
        this.$store
          .dispatch("chaptersStore/setChapterText", result.data.chapter)
          .then((res) => {
            this.chapterTextList.push(res);
            let order = this.bookState.bookOrder;
            order++;
            let state = Object.assign(this.bookState);
            state.bookOrder = order;
            state.historyTitle = this.chapterText.title;
            state.time = new Date();
            // 更新书籍信息
            this.$store
              .dispatch("detailStore/setBookDetailBookState", state)
              .then((res) => {
                this.$store.dispatch("bookrackStore/addHistoryData", res);
              });
            // console.log(this.bookDetail);
            this.$refs["viewBox"].scrollTop = this.scrollTop;
            if (order <= this.$store.state.detailStore.directoryList.length) {
              // this.$store.dispatch("chaptersStore/setChapterHistory", order);
            }
          });
      });
    },

    // 点击获取下一章
    goNextChapters() {
      if (
        this.bookState.bookOrder + 1 >=
        this.$store.state.detailStore.directoryList.length
      ) {
        this.$toast("没有下一章了");
        return;
      }
      let link = this.$store.state.detailStore.directoryList[
        this.bookState.bookOrder + 1
      ].link;
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/chapters/" + encodeURIComponent(link),
      }).then((result) => {
        this.$toast.clear();
        // console.log(result);
        this.$store
          .dispatch("chaptersStore/setChapterText", result.data.chapter)
          .then((res) => {
            this.chapterTextList = [];
            this.chapterTextList.push(res);
            this.$refs["viewBox"].scrollTop = 0;
            let order = this.bookState.bookOrder;
            order++;
            let state = Object.assign(this.bookState);
            state.bookOrder = order;
            state.historyTitle = this.chapterText.title;
            state.time = new Date();
            // 更新书籍信息
            this.$store
              .dispatch("detailStore/setBookDetailBookState", state)
              .then((res) => {
                this.$store.dispatch("bookrackStore/addHistoryData", res);
              });
          });
      });
    },

    // 点击获取上一章
    goPrevChapters() {
      if (this.bookState.bookOrder <= 0) {
        this.$toast("没有上一章了");
        return;
      }
      let link = this.$store.state.detailStore.directoryList[
        this.bookState.bookOrder - 1
      ].link;
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/chapters/" + encodeURIComponent(link),
      }).then((result) => {
        this.$toast.clear();
        // console.log(result);
        this.$store
          .dispatch("chaptersStore/setChapterText", result.data.chapter)
          .then((res) => {
            this.chapterTextList = [];
            this.chapterTextList.push(res);
            this.$refs["viewBox"].scrollTop = 0;
            let order = this.bookState.bookOrder;
            order--;
            let state = Object.assign(this.bookState);
            state.bookOrder = order;
            state.historyTitle = this.chapterText.title;
            state.time = new Date();
            // 更新书籍信息
            this.$store
              .dispatch("detailStore/setBookDetailBookState", state)
              .then((res) => {
                this.$store.dispatch("bookrackStore/addHistoryData", res);
              });
          });
      });
    },

    // 字号加减
    fontSizeDown() {
      if (this.attributeData.fontSize <= 12) {
        this.$toast("字号已经最小了");
        return;
      }
      this.attributeData.fontSize--;
    },
    fontSizeUp() {
      if (this.attributeData.fontSize >= 40) {
        this.$toast("字号已经最大了");
        return;
      }
      this.attributeData.fontSize++;
    },

    // 间距加减
    lineHeightDown() {
      if (this.attributeData.lineHeight <= 1) {
        this.$toast("间距已经最小了");
        return;
      }
      this.attributeData.lineHeight -= 0.5;
    },
    lineHeightUp() {
      if (this.attributeData.lineHeight >= 3) {
        this.$toast("间距已经最大了");
        return;
      }
      this.attributeData.lineHeight += 0.5;
    },

    // 背景选择
    changeBgc(index) {
      this.changeActive(index, this.attributeData.bgColor,"主题已更换");
      this.attributeData.bgc = this.attributeData.bgColor[index].bgc;
      this.attributeData.fontc = this.attributeData.bgColor[index].fontc;
      this.attributeData.toolc = this.attributeData.bgColor[index].toolc;
    },

    changeActive(index, arr,text) {
      if (arr[index].active) return;
      arr.map((v) => {
        if (v.active) {
          v.active = false;
        }
      });
      arr[index].active = true;
      this.$toast(text);
    },

    changeWay(index){
       this.changeActive(index, this.attributeData.changeWay,"已更换为"+this.attributeData.changeWay[index].text);
       this.isSideMole = this.attributeData.changeWay[index].way;
      //  console.log(this.isSideMole)
    },

    // 切换夜间与白天模式
    changeSun() {
      this.isSun = !this.isSun;
      if (this.isSun) {
        this.changeBgc(0);
        this.$toast("白天模式");
      } else {
        this.changeBgc(4);
        this.$toast("夜间模式");
      }
    },

    showPopup() {
      this.isShow = false;
      this.popShow = true;
    },
    // 改变排列顺序
    changeSort() {
      this.list = [];
      this.isSort = !this.isSort;
      this.dirList.reverse();
      // console.log(this.$store.state.detailStore.directoryList);
      this.onLoad();
    },

    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        for (let i = 0; i < 30; i++) {
          this.list.push(this.dirList[this.list.length]);
        }
        // 加载状态结束
        this.loading = false;
        // 数据全部加载完成
        if (this.list.length >= this.directoryList.length) {
          this.finished = true;
        }
      }, 500);
    },

    // 点击目录章节
    goChapters(item) {
      // console.log(item);
      let link = this.$store.state.detailStore.directoryList[item.order - 1]
        .link;
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/chapters/" + encodeURIComponent(link),
      }).then((result) => {
        this.$toast.clear();
        // console.log(result);
        this.$store
          .dispatch("chaptersStore/setChapterText", result.data.chapter)
          .then((res) => {
            this.popShow = false
            this.chapterTextList = [];
            this.chapterTextList.push(res);
            this.$refs["viewBox"].scrollTop = 0;
            let state = Object.assign(this.bookState);
            state.bookOrder = item.order - 1;
            state.historyTitle = this.chapterText.title;
            state.time = new Date();
            // 更新书籍信息
            this.$store
              .dispatch("detailStore/setBookDetailBookState", state)
              .then((res) => {
                this.$store.dispatch("bookrackStore/addHistoryData", res);
              });
          });
      });
    },
  },
};
</script>
